@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    border: solid 1px red;
}

body .outer-container {
    display: flex;
    height: 100vh;
    flex-direction: row;
    background-color: yellow;
}

main .inner-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 1200px;
    align-items: center;
}

.world-map {
    width: 100%;
}

.flag {
    width: 100px;
    height: 50px;
}

.inner-list-item {
    display: flex;
    flex-direction: row;
}

li {
    display: flex;
    flex-direction: column;
    height: 120px;
    overflow: auto;
    align-items: flex-start;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    padding: 12px;
}

ul {
    /*-webkit-column-count: 3;*/
    /*-moz-column-count: 3;*/
    /*column-count: 3;*/
    width: 80%;
    column-count:3;
    column-width:30em;
    border: 1px solid grey;
    list-style: none;
}

.blue {
    color: blue;
}

.green {
    color: green;
}

.red {
    color: red;
}

.yellow {
    color: yellow;
}

.purple {
    color: purple;
}

.orange {
    color: orange;
}

